.content-wrapper {
    width: 80%;
    margin: 0 auto;
}

.header {
    padding-top: 3em;
    margin: 0;
    height: 10vh;
    margin: 0 auto;
    text-align: center;

    background: linear-gradient(38deg, #20bc94, #757eba, #804598);
    background-size: 600% 600%;
    -webkit-animation: gradient-rotate 29s ease infinite;
    animation: gradient-rotate 29s ease infinite 
}

@-webkit-keyframes gradient-rotate {
    0%{background-position:0% 51%}
    50%{background-position:100% 50%}
    100%{background-position:0% 51%}
}

@keyframes gradient-rotate {
    0%{background-position:0% 51%}
    50%{background-position:100% 50%}
    100%{background-position:0% 51%}
}

.footer {
    color: #fff;
    font-family: 'Raleway', sans-serif;
    padding: 1em 0;
    margin: 0;
    height: auto;
    margin: 6em auto 0 auto;
    text-align: center;

    background: linear-gradient(38deg, #20bc94, #757eba, #804598);
    background-size: 600% 600%;
    -webkit-animation: gradient-rotate 29s ease infinite;
    animation: gradient-rotate 29s ease infinite 
}

@keyframes gradient-rotate {
    0%{background-position:0% 51%}
    50%{background-position:100% 50%}
    100%{background-position:0% 51%}
}
    

.footer > p > a {
    color: rgb(255, 166, 0);  
    text-decoration: none;
    transition-duration: 1s;
}

.footer > p > a:hover {
    color: #fff;    
}

/* ------------- */

/* Navigation styles */

.nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
.nav li {
    display: inline-block;
    margin-right: 1em;
    font-family: 'Oswald', sans-serif;
    font-size: 1.5em;
    line-height: 40px;
    height: 40px;
    border-radius: 25px;
}

.nav a {
    text-decoration: none;
    padding: 0 1em;
    color: rgb(0, 0, 0);
    display: block;
    transition: 0.5s;
    border-radius: 25px;
}

.nav a:hover {
    background-color: #fff;
    color: #000;
    border-radius: 25px;
}

.nav a:active { 
    background-color: #000;
    color: #fff;
    
}


/* ------------------------------ */

/* Styles for headings */

.title {
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 5rem;
    font-weight: 100;
    text-align: center;
    padding: 0.5em 0;
}

.sub-title {
    color: #000;
    font-family: 'Raleway', sans-serif;
    font-size: 2rem;
    font-weight: 100;
    text-align: center;
    padding: 1.2em 0; 
}

.sub-title > a {
    color: rgba(32,124,229,1);
    transition-duration: 1s;
}

.sub-title > a:hover {
    color: rgb(255, 166, 0);
}

.landing-title {
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 6rem;
    margin-bottom: 0.3em;
    font-weight: 100;
    text-align: center;
  }
  
.landing-desc {
    font-family: 'Raleway', sans-serif;
    font-weight: 200;
    font-size: 1.4em;
    margin-top: 1em;
    text-align: left;
    background: rgba(0, 0, 0, 0.075);
    padding: 2em;
    border-radius: 25px;
  }

/* -------------- */

/* Scroll button */

.scroll-btn {
    width: 18%;
    font-size: 2.2em;
    display: block;
    background: linear-gradient(38deg, #20bc80, #8586d3, #c94fb9);
    background-size: 600% 600%;
    -webkit-animation: gradient-rotate 29s ease infinite;
    animation: gradient-rotate 29s ease infinite;
    border-radius: 25px;
    text-shadow: 2px 2px 7px #666666;
    box-shadow: 1px 1px 3px #666666;
    font-family: 'Raleway', sans-serif;
    color: #ffffff;
    padding: 0.2em 0.4em;
    cursor: pointer;
    text-align: center;
    margin: 0 auto 10em auto;
    text-decoration: inherit;
    transition: 1s;
  }

  .scroll-btn:hover {
    background: linear-gradient(38deg, #8ffadf, #acb5ee, #c78bdf);
    transition: 1s;
  }

  object > p {
      font-family: 'Open Sans', sans-serif;
  }

  object > p > a {
    color: rgba(32,124,229,1);
  }

  object > p > a:hover {
    color: rgb(255, 166, 0);
  }

  /* -------------- */

/* Contact section styles */
.contact-title {
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 5rem;
    font-weight: 100;
    text-align: center;
    padding: 1.2em 0 0.5em 0;
}

.contact-box {
    background: rgba(0, 0, 0, 0.5);
    width: 40%;
    height: auto;
    padding: 1em;
    margin: 0 auto 3em auto;
    border-radius: 25px;
}

.contact-box > p {
    color: #fff;
    text-align: center;
    font-family: 'Raleway', sans-serif;
    font-size: 1.5em;
}

.contact-box > p > a {
    color: rgb(255, 166, 0);
    transition-duration: 1s;
    margin-left: .5em;
}

.contact-box > p > a:hover {
    color: #fff;
}

/* ------------------------- */

/* About section styles */
.about-title {
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 5rem;
    font-weight: 100;
    margin: 0;
    text-align: center;
    padding: 0.6em 0;
}

.aboutTextContainer{
    background: rgba(0, 0, 0, 0.075);
    border-radius: 25px;
    padding: 2em;
    margin: 0 auto 3em auto;
    width: 60%;
}

.about-text {
    font-family: 'Raleway', sans-serif;
    font-size: 1.3em;
    color: #000;
    text-align: left;
    padding: 0.5em 0;
    margin: 0;
}

.skillset{
    margin: auto auto;
    width: 60%;
    padding: 2em;
    background: rgba(0, 0, 0, 0.075);
    vertical-align: center;
    border-radius: 25px;
}
.skillset ul {
    text-align: left;
    font-family: 'Raleway', sans-serif;
}

.skillset ul > li{
    padding: 0.4em 0;
}

/* ---------------------- */

/*Creations page project list*/

.projectlist {

}

.project{
    background: rgba(0, 0, 0, 0.075);
    width: 100%;
    padding: 1em 1em;
    margin-bottom: 2em;
    border-radius: 25px;
}

.project-title {
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 2em;
    font-weight: 100;
    text-align: center;
    padding: 0.5em auto;
}

.project-body{
    font-family: 'Raleway', sans-serif;
    font-size: 1.2em;
    color: #000; 
}

.project-bodyText {
    width: 60%;
    margin: 1em auto 3em auto;
    background: rgba(255, 255, 255, 0.6);
    padding: 1em;
    text-indent: 1em;
    border-radius: 25px;
}

.project-body > a {
    text-align: center;
    margin: 0 auto;
    padding: 0 auto;
    text-decoration: none;
}

.project > img{
    width: 100%;
    height: auto;
    margin: 0 auto;
    align-self: center;
}

.project-footer{
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 1em;
    font-weight: 100;
    text-align: center;
    padding: 0.6em 0 2em 0;
}

.project-btn {
    width: 18%;
    font-size: 1.5em;
    display: block;
    background: linear-gradient(#739fff, #588dff);
    border-radius: 25px;
    text-shadow: 2px 2px 7px #666666;
    box-shadow: 1px 1px 3px #666666;
    font-family: 'Oswald', sans-serif;
    color: #ffffff;
    padding: 0.2em 0.4em;
    cursor: pointer;
    margin: 0 auto;
  }

  .project-btn:hover {
    background: linear-gradient(#a0beff, #6c9aff);
  }

  .video-container {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 30px; height: 0; overflow: hidden;
    }
    
    .video-container iframe,
    .video-container object,
    .video-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    }

/* ----------------------------- */

/* Media queries */

@media (max-width: 1100px) {

    .title {
        font-size: 3rem;
    }

    #topBtn {
        font-size: 1.2rem;
        position: fixed;
        bottom: 3.5em;
        right: 1em;
    }

    .scroll-btn {
        width: 40%;
        font-size: 2rem;
    }
    
    .contact-box {
        width: 70%;
    }

    .contact-box > p {
        font-size: 1rem;
    }

    .contact-title {
        font-size: 3rem;
    }

    .project-btn {
        font-size: 1.5em;
        width: 30%;
      }

    .project-body > p {
        width: 70%;
        margin: 1em auto 3em auto;
    }

    .project-bodyText {
        width: 70%;
    }

    .about-text {
        font-size: 1.1em;
        padding: 0.7em 0;
    }

    .landing-title {
        font-size: 3rem;
      }
  
    .landing-desc {
        font-size: 1rem;
        width: 60%;

    }

    .aboutTextContainer{
        width: 70%;
    }

}

@media (max-width: 768px) {

    .nav li {
        padding: 0.2em 0;
        font-size: 1.2em;
        width: 30%;
        margin: 0 auto;
    }

    .title {
        font-size: 2rem;
        padding: 1em;
    }

    .sub-title {
        font-size: 1.5rem;
    }

    .header {
        padding-top: 2em;
    }

    #topBtn {
        font-size: 1rem;
        position: fixed;
        bottom: 4em;
        right: 1em;
    }

    .scroll-btn {
        width: 40%;
        font-size: 2rem;
    }
    
    .contact-box {
        width: 80%;
    }

    .contact-box > p {
        font-size: 1rem;
        text-align: center;
    }

    .contact-title {
        font-size: 2rem;
        margin: 0;
    }

    .footer {
        font-size: 1rem;
    }

    .scroll-btn {
        font-size: 1.5em;
    }

    .project-btn {
        font-size: 1em;
        width: 40%;
    }

    .project-body {
        width: 100%;
    } 

    .project-body > p {
        width: 90%;
        margin: 1em auto 3em auto;
    }

    .skillset{
        width: 80%;
        padding: 1.5em;
    }
    
    .about-text {
        font-size: 1em;
        padding: 0.5 0;
        margin: 0;
    }

    .landing-title {
        font-size: 2rem;
      }
    
    .landing-desc {
        font-size: 1rem;
    }

    .project-bodyText {
        font-size: 1rem;
        width: 80%;
    }

    .header{
        height: 25vh;
    }

    .nav li {
        display: block;
    }

    .aboutTextContainer{
        width: 80%;
    }
}
